import React from 'react'
import { LineItem } from './LineItem'
import { getContentfulProduct } from '../../../utils/returns'

export function StageOne({
  returnLineItems,
  formData,
  handleChange,
  setValidationError,
  saveReturnLineItem,
  nodes,
  setOutOfStockForReplace,
  storeCreditReturnsMessage,
}) {
  return (
    <>
      {returnLineItems.map(
        ({ lineItem, reasonCode, returnLineItemReview, exchangeOption }, i) => (
          <LineItem
            key={lineItem.id}
            lineItem={lineItem}
            reasonCode={reasonCode}
            returnLineItemReview={returnLineItemReview}
            contentfulProduct={getContentfulProduct(nodes, lineItem)}
            formData={formData?.[lineItem?.id] ?? {}}
            handleChange={handleChange}
            setValidationError={setValidationError}
            saveReturnLineItem={saveReturnLineItem}
            initialExchangeOption={exchangeOption}
            onOutOfStockForReplace={setOutOfStockForReplace}
            storeCreditReturnsMessage={storeCreditReturnsMessage}
          />
        ),
      )}
    </>
  )
}
