import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'

import { desktopVw } from '../../styles/utils'
import { decodeIdFromGraphQLResponse } from '../../utils'

import useShopifyProduct from '../../hooks/useShopifyProduct'
import useSelectedOptions from '../../hooks/useSelectedOptions'

import ProductSizeSelector from '../ProductSizeSelector'
import ProductColorSelector from '../ProductColorSelector'

const StyledLineItemSelect = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(24)};
    flex-direction: column;
  }
`

const ImageContainer = styled.div`
  display: flex;
  flex-direction: row;
  padding-bottom: 15px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    flex-direction: row;
  }
`

const MobileTitle = styled.div`
  font-size: 12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const Title = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    font-size: ${desktopVw(16)};
    margin: 0 0 ${desktopVw(14)};
  }
`

const ImageWrapper = styled.div`
  width: 80px;
  height: 104px;
  flex-shrink: 0;
  margin: 0 12px 0 0;
  background: ${props => props.theme.colors.greyLighter};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(13)} 0 0;
    width: ${desktopVw(80)};
    height: ${desktopVw(104)};
  }
`
const StyledImage = styled(Image)`
  width: 100%;
  height: 100%;
`

const Body = styled.div``

const SizeWrapper = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(10)};
  }
`

const StyledSizeSelector = styled(ProductSizeSelector)`
  border-top: none;
  justify-content: start;
`

const ExchangeLineItemSelector = ({
  onLineItemSelect,
  product,
  startingColor,
  startingSize,
  lineItem,
  clearValidationError,
  handleOutOfStock,
}) => {
  const { product: p } = useShopifyProduct(product.shopifyProduct.handle)
  const [oneSize, setOneSize] = useState(false)
  const [sizes, setSizes] = useState(null)
  const [shopifyColors, setShopifyColors] = useState([])
  const [outOfStock, setOutOfStock] = useState(false)
  const newStartingSize =
    startingSize ||
    (oneSize ? sizes[0] : lineItem.variantTitle.split(' / ')[0].trim())

  const [productImages, setProductImages] = useState([])

  useEffect(() => {
    if (!p) return
    const sizeOption = p.options.find(({ name }) => name === 'Size')
    const colorOption = p.options.find(({ name }) => name === 'Color')

    if (sizeOption) {
      setSizes(sizeOption.values)
    }

    if (colorOption) {
      setShopifyColors(colorOption.values)
    }
  }, [p])

  const newStartingColor =
    startingColor ||
    lineItem.variantTitle
      .split(' / ')
      .filter(v => v !== '-')
      .pop()
      .trim()

  const {
    handleChange,
    selectedSize,
    selectedColor,
    productVariant,
  } = useSelectedOptions(
    product.shopifyProduct.handle,
    newStartingSize,
    newStartingColor,
  )

  useEffect(() => {
    const selectedColorImages = product.productImages.filter(
      ({ color }) => color.name === selectedColor,
    )

    setProductImages(selectedColorImages)
  }, [selectedColor])

  useEffect(() => {
    if (!productVariant || !productVariant.variantBySelectedOptions) return

    clearValidationError && clearValidationError()

    const variantId = decodeIdFromGraphQLResponse(
      productVariant.variantBySelectedOptions.id,
    )

    onLineItemSelect(parseFloat(variantId), {
      size: oneSize ? sizes[0] : selectedSize,
      color: selectedColor,
      price: parseFloat(productVariant.variantBySelectedOptions.priceV2.amount),
    })

    if (productVariant && productVariant.variantBySelectedOptions) {
      const availableQuantity =
        productVariant.variantBySelectedOptions.quantityAvailable
      setOutOfStock(availableQuantity <= 0)
      handleOutOfStock(availableQuantity <= 0)
    }
  }, [productVariant, selectedColor, selectedSize])

  useEffect(() => {
    if (!sizes) return

    if (sizes.length === 1) {
      setOneSize(true)
    }
  }, [sizes])

  return (
    <StyledLineItemSelect>
      <ImageContainer>
        <ImageWrapper>
          {productImages.length > 0 && (
            <StyledImage fluid={productImages[0].images[0].fluid} />
          )}
        </ImageWrapper>
        <MobileTitle>Select color and size for exchange</MobileTitle>
        <Title>Select color and size for exchange</Title>
      </ImageContainer>

      <div>
        {product && (
          <ProductColorSelector
            colors={product.colors}
            onChange={handleChange}
            selected={selectedColor}
            shopifyColors={shopifyColors}
            productTitle={
              product.title ||
              (product.shopifyProduct && product.shopifyProduct.title)
            }
            showPrice={false}
          />
        )}
        {p && sizes && (
          <SizeWrapper>
            <StyledSizeSelector
              sizes={sizes}
              onChange={handleChange}
              selected={selectedSize}
              selectedColor={selectedColor}
              variants={p.variants.edges}
              sizeGuide={product.sizeGuide}
              sizeGuideJson={product.sizeGuideJson}
              isOutOfStock={outOfStock}
            />
          </SizeWrapper>
        )}
      </div>
    </StyledLineItemSelect>
  )
}

export default ExchangeLineItemSelector
