import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../styles/utils'
import Star from '../../assets/landing_star.svg'

const FeedbackStar = styled(Star)`
  path {
    fill: ${({ fill, theme }) =>
      fill ? theme.colors.primary : theme.colors.secondary};
    stroke: ${({ fill, theme }) =>
      fill ? theme.colors.secondary : theme.colors.primary};
  }
`

const Rate = styled.div`
  position: relative;
  margin-right: 2px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(4.5)};
  }
`

const Input = styled.input`
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  cursor: pointer;

  &:checked {
    + svg {
      .star {
        &__inner {
          fill: ${props => props.fill && props.theme.colors.yellow};
        }
      }
    }
  }
`

export const StarRating = ({ onClick, initialValue = 0 }) => {
  const [value, setValue] = useState(parseInt(initialValue))
  useEffect(() => {
    setValue(parseInt(initialValue))
  }, [initialValue])
  return [...Array(5)].map((_, i) => (
    <Rate>
      <Input
        key={i}
        label='feedback'
        type='radio'
        value={i + 1}
        name='feedback'
        onChange={() => {
          setValue(i + 1)
          if (onClick) onClick(i + 1)
        }}
        aria-label={`${i} star`}
      />
      <FeedbackStar
        fill={i < value ? 1 : 0}
        className={`StarRating__Feedback-svg ${i < value ? 'svg_filled' : ''}`}
      />
    </Rate>
  ))
}
