import React, { useState } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import StarIcon from '../../assets/star.svg'

const StyledProductRating = styled.form`
  display: flex;
  justify-content: space-between;
  margin: 0 50px 0 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(80)} 0 0;
  }
`

const Star = styled(StarIcon)`
  width: 14px;
  height: 14px;

  .star {
    &__stroke {
      fill: ${props => props.theme.colors.primary};
    }
    &__inner {
      fill: ${props => props.fill && props.theme.colors.primary};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(20)};
    height: ${desktopVw(20)};
  }
`

const Input = styled.input`
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;

  &:checked {
    + svg {
      .star {
        &__inner {
          fill: ${props => props.fill && props.theme.colors.yellow};
        }
      }
    }
  }
`

const Rate = styled.div`
  position: relative;
  margin-right: 2px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(4.5)};
  }
`

const RatingInputWrapper = styled.div`
  display: flex;
  flex-direction: column;
  font-size: 12px;

  span {
    margin-right: 10px;
    margin-bottom: 5px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};

    span {
      margin-right: ${desktopVw(10)};
    }
  }
`

const StyledRatingInput = styled.div`
  display: flex;
  align-items: flex-start;
`

function StarRating({ ratingObj, onChange, initialValue }) {
  const [value, setValue] = useState(initialValue)

  return (
    <StyledRatingInput>
      {[...Array(5)].map((_, i) => (
        <Rate>
          <Input
            label={ratingObj.label}
            type='radio'
            value={i + 1}
            name={ratingObj.name}
            onChange={e => {
              setValue(i + 1)
              onChange(e)
            }}
            aria-label={`${i} star`}
          />
          <Star fill={i < value ? 1 : 0} />
        </Rate>
      ))}
    </StyledRatingInput>
  )
}

const ProductRating = ({ handleChange, ratings, formData }) => {
  return (
    <StyledProductRating>
      {ratings.map(r => (
        <RatingInputWrapper key={r.name}>
          <span>{r.label}</span>
          <StarRating
            ratingObj={r}
            onChange={handleChange}
            initialValue={Number(formData?.[r.name])}
          />
        </RatingInputWrapper>
      ))}
    </StyledProductRating>
  )
}

export default ProductRating
