import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../../styles/utils'

import UploadThumbnail from './UploadThumbnail'
import CameraIcon from '../../../assets/camera.svg'

const StyledImageUpload = styled.div`
  position: relative;
  box-sizing: border-box;
  border: 1px solid ${props => props.theme.colors.greyLight};
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(38)};
    padding: 0;
    width: 100%;
  }
`

const Inner = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 16px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(10)} ${desktopVw(10)};
  }
`

const Camera = styled(CameraIcon)`
  min-width: 13.5px;
  margin-right: 9px;
  height: 23px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(33)};
    height: ${desktopVw(23)};
  }
`

const Input = styled.input`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
`

const ThumbnailList = styled.ul`
  display: flex;
  flex-flow: wrap;
  margin-bottom: ${props => (props.spacingL ? '32px' : '20px')};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${props => (props.spacingL ? desktopVw(36) : desktopVw(20))};
  }
`

const ThumbnailItem = styled.li`
  margin: 0 10px 10px 0;

  &:nth-child(3n + 3) {
    margin-right: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(10)} ${desktopVw(10)} 0;

    &:nth-child(3n + 3) {
      margin: 0 ${desktopVw(10)} ${desktopVw(10)} 0;
    }

    &:nth-child(5n + 5) {
      margin-right: 0;
    }
  }
`

const ImageWithTextLayout = styled.div`
  cursor: pointer;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    align-items: center;
  }
`

export const ImageUpload = ({
  className,
  placeholder,
  name,
  required = false,
  imageApiUrl,
  handleFiles,
  photos,
  setPhotos,
  removePhoto,
}) => {
  return (
    <>
      {photos.length > 0 && (
        <ThumbnailList>
          {photos.map((p, i) => (
            <ThumbnailItem key={`${p}${i}`}>
              <UploadThumbnail
                fileName={p.name}
                imageApiUrl={imageApiUrl}
                file={p.file}
                base64String={p.value}
                photos={photos}
                removePhoto={removePhoto}
                setPhotos={setPhotos}
              />
            </ThumbnailItem>
          ))}
        </ThumbnailList>
      )}
      <ImageWithTextLayout>
        <StyledImageUpload>
          <Inner>
            <Camera />
          </Inner>
          <Input
            className={className}
            placeholder={placeholder}
            required={required}
            name={name}
            type='file'
            onChange={e => handleFiles(e)}
            accept='image/png, image/jpeg'
            multiple
          />
        </StyledImageUpload>
      </ImageWithTextLayout>
    </>
  )
}
