import React from 'react'
import styled from 'styled-components'
import useDetectDevices from '../../hooks/useDetectDevices'
import BackLink from '../BackLink'
import { desktopVw } from '../../styles/utils'

const TimeLineWrapper = styled.div`
  display: flex;
  align-items: center;
  margin: 34px 0 25px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(62)};
    margin-bottom: ${desktopVw(26)};
  }
`

const TimeLine = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
`

const Circle = styled.div`
  width: 13px;
  height: 13px;
  background: ${({ isComplete, theme }) =>
    isComplete ? theme.colors.primary : theme.colors.secondary};
  border: 1px solid
    ${({ isComplete, theme }) =>
      isComplete ? theme.colors.primary : theme.colors.silver};
  border-radius: 50%;
`

const Dashed = styled.div`
  width: 100px;
  border: 1px dashed
    ${({ isComplete, theme }) =>
      isComplete ? theme.colors.primary : theme.colors.silver};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(126)};
  }
`

export function Timeline({ activeStep, steps = 3 }) {
  const { devices } = useDetectDevices()

  return (
    <TimeLineWrapper>
      {activeStep > 0 && devices.desktop && <BackLink />}
      <TimeLine>
        {[...new Array(steps)].map((_, step) => {
          if (step === steps - 1)
            return <Circle isComplete={activeStep >= step} />
          return (
            <>
              <Circle isComplete={activeStep >= step} />
              <Dashed isComplete={activeStep >= step + 1} />
            </>
          )
        })}
      </TimeLine>
    </TimeLineWrapper>
  )
}
