import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../../styles/utils'

// Components
import LineItemThumbnail from '../../Account/LineItemThumbnail'
import Button from '../../Button'

// Stage components
import ProductRating from '../ProductRating'

import {
  Alert,
  Container,
  CustomSelect,
  Select,
  TagSelection,
  TextArea,
} from '../../FormElements'

import {
  RATINGS_MAP,
  REASON_CATEGORY_MAP,
  REASON_COMMENTS_MAP,
  RETURN_REASONS,
  SUB_REASON_MAP,
} from '../../Account/ReturnsModal/returnModalData'

import {
  getImageUrls,
  getQuantitiesRemainingToReturn,
} from '../../../utils/returns'
import Drawer from '../../common/Drawer'
import { ImageUpload } from '../../common/ImageUpload'

const StyledLineItem = styled(LineItemThumbnail)`
  margin: 0;
  .line_item_thumbnail__content {
    flex-direction: column;
  }
  .line_item_thumbnail__detail {
    margin: 3px 0;
  }
`

const Form = styled.form`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-top: ${desktopVw(26.5)};
  }
`

const Text = styled.div`
  font-size: 12px;
  margin: 15px 0 10px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin: 0 0 ${desktopVw(10)};
  }
`

const Footer = styled.div`
  margin-top: 30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(20)};
  }
`

const AlertContainer = styled.div`
  margin-bottom: 16px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(16)};
  }
`

const ButtonContainer = styled.div`
  display: flex;
  flex-direction: column-reverse;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
  }
`

const Back = styled(Button)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(10)} 0 0;
    padding-left: ${desktopVw(62)};
    padding-right: ${desktopVw(62)};
  }
`

const StyledButton = styled(Button)`
  display: block;
  width: 100%;
  margin: 0 0 10px;
  padding: 10px;
  span {
    font-size: 12px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    span {
      font-size: ${desktopVw(12)};
      text-transform: uppercase;
    }
  }
`

const QuantitySelect = styled(Select)`
  flex-shrink: 0;
  flex: 1;
  font-size: 16px;
  margin-right: 0px;
  margin-top: 10px;
  width: 59px;

  select {
    text-transform: uppercase;
    font-size: 12px;
    padding: 6px 10px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(65)};
    margin-top: ${desktopVw(10)};
    select {
      font-size: ${desktopVw(12)};
      padding: ${desktopVw(6)} ${desktopVw(12)};
    }
  }
`

function isPhotoUploadRequired(formData) {
  return ['DEFECTIVE_ITEM', 'DAMAGED_PACKAGE'].includes(formData?.reasonCode)
}

const StageZeroDrawer = ({
  open,
  closeModal,
  lineItem,
  saveReturnLineItem,
  contentfulProduct,
  onSubmitSuccess,
  formData,
  validationError,
  setValidationError,
  handleChange,
  returnLineItem,
  photos,
  handlePhotosChange,
}) => {
  const imageUrl = `https://api.onequince.com/service/returns/upload-image?lineItemId=${lineItem?.id}`

  const [currentLineItemId, setCurrentLineItemId] = useState(null)

  const [localPhotos, setLocalPhotos] = useState(photos)

  const [productType, setProductType] = useState('default')

  const [returnQuantity, setReturnQuantity] = useState(
    Math.min(getQuantitiesRemainingToReturn(lineItem), 1),
  )

  const resetModal = () => {
    // Initialize the state
    setProductType('default')
    setReturnLineItemReview({})
    setSubReasons([])
    setValidationError('')
    setTextAreaPlaceholder('Comments')
  }

  const handleFiles = e => {
    if (!e.target.files) return
    const { files } = e.target

    for (let i = 0; i < files.length; i++) {
      const file = files[i]

      if (photos.some(({ name }) => name === file.name)) return

      const reader = new FileReader()
      reader.readAsDataURL(file)

      reader.onload = () => {
        setLocalPhotos(prevState => {
          const newPhotos = [
            ...prevState,
            { name: file.name, value: reader.result, response: '', file },
          ]
          handlePhotosChange(newPhotos)
          return newPhotos
        })
      }

      reader.onerror = function(error) {
        console.log('Error: ', error)
      }
    }
  }

  const removePhoto = fileName => {
    const filteredPhotos = photos.filter(({ name }) => name !== fileName)
    setLocalPhotos(filteredPhotos)
    handlePhotosChange(filteredPhotos)
  }

  useEffect(() => {
    if (!contentfulProduct) return
    if (contentfulProduct.productTypeName) {
      setProductType(contentfulProduct.productTypeName.toLowerCase())
    }
  }, [contentfulProduct])

  useEffect(() => {
    if (!lineItem) return
    setReturnQuantity(Math.min(getQuantitiesRemainingToReturn(lineItem), 1))
    if (!currentLineItemId) {
      setCurrentLineItemId(lineItem.id)
    } else if (currentLineItemId != lineItem.id) {
      setCurrentLineItemId(lineItem.id)
      resetModal()
    }
  }, [lineItem])

  const [returnLineItemReview, setReturnLineItemReview] = useState(
    returnLineItem?.returnLineItemReview ?? {},
  )
  const [subReasons, setSubReasons] = useState([])

  const [textAreaPlaceholder, setTextAreaPlaceholder] = useState('Optional')

  useEffect(() => {
    if (!formData.reasonCode) return

    const subReasons =
      SUB_REASON_MAP[
        REASON_CATEGORY_MAP[
          (contentfulProduct?.productTypeName ?? '').toLowerCase()
        ] || REASON_CATEGORY_MAP.default
      ][formData.reasonCode]

    setSubReasons(subReasons)
    setTextAreaPlaceholder(REASON_COMMENTS_MAP[formData.reasonCode])
  }, [formData])

  const handleChangeReview = e => {
    setValidationError('')

    const { target } = e
    const { name } = target
    const { value } = target

    setReturnLineItemReview({
      ...returnLineItemReview,
      [name]: value,
    })
  }

  const valid = () => {
    if (!formData.reasonCode) {
      setValidationError('Please select a reason before proceeding')

      return false
    }

    if (isPhotoUploadRequired(formData) && (photos || []).length === 0) {
      setValidationError('Please upload a photo before proceeding')

      return false
    }

    if (subReasons.length > 0 && !returnLineItemReview?.subReason) {
      setValidationError('Please select an additional reason before proceeding')

      return false
    }

    for (const r of RATINGS_MAP[
      REASON_CATEGORY_MAP[productType] || REASON_CATEGORY_MAP.default
    ]) {
      if (!returnLineItemReview[r.name]) {
        setValidationError(r.error)
        return false
      }
    }

    setValidationError('')
    return true
  }

  const handleSubmit = () => {
    if (!valid()) return

    saveReturnLineItem(
      {
        ...formData,
        lineItemId: lineItem.id,
        returnLineItemReview: {
          ...returnLineItemReview,
          reason: formData.reasonCode,
          returnLineItemId: lineItem.id,
        },
        lineItem,
        imageUrls: getImageUrls(photos),
      },
      contentfulProduct,
      returnQuantity,
    )

    onSubmitSuccess()
    closeModal()
  }

  function handleQuantityChange(e) {
    const quantitiesRemainingToReturn = getQuantitiesRemainingToReturn(lineItem)
    const { value } = e.target
    if (value <= 0) setReturnQuantity(Math.min(quantitiesRemainingToReturn, 1))
    else if (value > quantitiesRemainingToReturn)
      setReturnQuantity(Number(quantitiesRemainingToReturn))
    else setReturnQuantity(Number(value))
  }

  return (
    <Drawer open={open} onClose={closeModal}>
      <StyledLineItem secondaryLayout lineItem={lineItem} />

      <Form>
        {getQuantitiesRemainingToReturn(lineItem) > 1 && (
          <Container>
            <Text>Quantity</Text>
            <QuantitySelect
              options={[
                ...new Array(getQuantitiesRemainingToReturn(lineItem)),
              ].map((_, idx) => idx + 1)}
              onChange={handleQuantityChange}
            />
          </Container>
        )}
        <Container>
          <Text>Reason</Text>
          <CustomSelect
            label='Select Reason'
            name='reasonCode'
            required
            options={RETURN_REASONS}
            onChange={e => {
              setReturnLineItemReview({
                ...returnLineItemReview,
                subReason: null,
              })
              setLocalPhotos([])
              handlePhotosChange([])
              handleChange(e)
            }}
            defaultOption={
              RETURN_REASONS.find(rObj => rObj.value === formData?.reasonCode)
                ?.name
            }
          />
        </Container>
        {formData.reasonCode && subReasons.length > 0 && (
          <Container>
            <Text>Additional Reason</Text>
            <CustomSelect
              label='Select Additional Reason'
              name='subReason'
              required
              options={(subReasons || []).map(reason => ({
                value: reason,
                name: reason,
              }))}
              onChange={handleChangeReview}
              defaultOption={returnLineItemReview?.subReason}
            />
          </Container>
        )}
        {isPhotoUploadRequired(formData) && (
          <Container>
            <Text>Upload Photo (Required)</Text>
            <ImageUpload
              imageApiUrl={imageUrl}
              handleFiles={handleFiles}
              photos={photos}
              setPhotos={handlePhotosChange}
              removePhoto={removePhoto}
            />
          </Container>
        )}
        <Container>
          <ProductRating
            ratings={
              RATINGS_MAP[
                REASON_CATEGORY_MAP[productType] || REASON_CATEGORY_MAP.default
              ]
            }
            handleChange={handleChangeReview}
            formData={returnLineItemReview}
          />
        </Container>
        <Container>
          <Text>Additional Comments</Text>
          <TextArea
            name='additionalComments'
            required
            placeholder={textAreaPlaceholder}
            onChange={handleChangeReview}
            value={returnLineItemReview?.additionalComments}
            height={100}
          />
        </Container>
      </Form>

      <Footer>
        {validationError && (
          <AlertContainer>
            <Alert message={validationError} />
          </AlertContainer>
        )}
        <ButtonContainer>
          <StyledButton black type='button' onClick={handleSubmit}>
            Submit
          </StyledButton>
        </ButtonContainer>
      </Footer>
    </Drawer>
  )
}

export default StageZeroDrawer
