import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import Tippy from '@tippyjs/react'
import useSelectedOptions from '../../../hooks/useSelectedOptions'
import {
  DetailValue,
  Edit,
  ProductOption,
  ReturnItem,
  StyledButton,
  StyledCheckbox,
  StyledLineItemThumbnail,
} from '../commons'
import { colors } from '../../../styles/theme'
import Drawer from '../../common/Drawer'
import ExchangeLineItemSelector from '../ExchangeLineItemSelector'
import { desktopVw } from '../../../styles/utils'
import useDetectDevices from '../../../hooks/useDetectDevices'
import { formatReasonCode, formatSubReason } from '../../../utils/returns'

import Bolt from '../../../assets/bolt.svg'
import Info from '../../../assets/info.svg'

import 'tippy.js/dist/tippy.css'

const DetailsWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: unset;
  }
`

const StyledCheckboxStageTwo = styled(StyledCheckbox)`
  label {
    color: ${({ theme }) => theme.colors.primary};
  }
  border: ${({ checked }) => checked && '0.35px solid #c4c4c4'};
  padding: ${({ checked }) => checked && '2px 8px 3px'};
  margin-left: ${({ checked }) => checked && '-9px'};
  margin-top: ${({ checked, showMarginTop }) =>
    checked && showMarginTop && '4px'};
  margin-right: ${({ checked }) => checked && 0};
  margin-bottom: 8px;
  &:last-child {
    margin-bottom: 0;
  }

  border-radius: 5px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: 0;
    margin-bottom: ${desktopVw(18)};
  }
`

const ExchangeWrapper = styled.div`
  font-size: 10px;
  display: flex;
  .exchange {
    font-weight: 500;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

export function LineItem({
  lineItem,
  reasonCode,
  returnLineItemReview,
  contentfulProduct,
  formData,
  handleChange,
  setValidationError,
  saveReturnLineItem,
  initialExchangeOption,
  onOutOfStockForReplace,
  storeCreditReturnsMessage,
}) {
  const { productVariant } = useSelectedOptions(
    contentfulProduct.shopifyProduct.handle,
    lineItem.variantTitle.split(' / ')[0].trim(),
    lineItem.variantTitle.split(' / ')[1].trim(),
  )
  const { productVariant: exchangeProductVariant } = useSelectedOptions(
    contentfulProduct.shopifyProduct.handle,
    initialExchangeOption?.size,
    initialExchangeOption?.color,
  )

  const [outOfStockForReplace, setOutOfStockForReplace] = useState(false)
  const [exchangeOption, setExchangeOption] = useState(initialExchangeOption)
  const [exchangeData, setExchangeData] = useState({})
  const [drawerOpen, setDrawerOpen] = useState(false)
  const [outOfStock, setOutOfStock] = useState(false)

  const handleOutOfStock = isOutOfStock => {
    setOutOfStock(isOutOfStock)
  }

  const setExchangeVariant = (id, option) => {
    setExchangeData({
      exchangeProductId: lineItem.productId,
      exchangeVariantId: id,
    })

    setExchangeOption(option)
  }

  function saveLineItem(e) {
    saveReturnLineItem(
      {
        ...formData,
        ...exchangeData,
        lineItemId: lineItem.id,
        returnType: e.target.value,
        returnLineItemReview: {
          ...returnLineItemReview,
          reason: formData.reasonCode,
          returnLineItemId: lineItem.id,
        },
        lineItem,
        exchangeOption,
      },
      contentfulProduct,
      1,
    )
  }

  const handleSubmit = () => {
    if (!exchangeOption) {
      setValidationError('Please select a size')
      return
    }
    const psuedoEvent = {
      target: { name: 'returnType', value: 'EXCHANGE', checked: true },
    }
    saveLineItem(psuedoEvent)
    setDrawerOpen(false)
    handleChange(psuedoEvent, lineItem?.id)
  }

  useEffect(() => {
    if (!productVariant || !productVariant.variantBySelectedOptions) return

    if (productVariant && productVariant.variantBySelectedOptions) {
      const availableQuantity =
        productVariant.variantBySelectedOptions.quantityAvailable
      if (formData?.returnType === 'REPLACEMENT') {
        handleCheckboxChange({
          target: {
            name: 'returnType',
            value: availableQuantity <= 0 ? 'REFUND' : 'REPLACEMENT',
            checked: true,
          },
        })
      }
      setOutOfStockForReplace(availableQuantity <= 0)
      if (onOutOfStockForReplace) onOutOfStockForReplace(availableQuantity <= 0)
    }
  }, [productVariant])

  useEffect(() => {
    if (
      !exchangeProductVariant ||
      !exchangeProductVariant.variantBySelectedOptions ||
      formData?.returnType !== 'EXCHANGE'
    )
      return

    if (
      exchangeProductVariant &&
      exchangeProductVariant.variantBySelectedOptions
    ) {
      const availableQuantity =
        exchangeProductVariant.variantBySelectedOptions.quantityAvailable
      if (availableQuantity <= 0) {
        handleCheckboxChange({
          target: {
            name: 'returnType',
            value: 'REFUND',
            checked: true,
          },
        })
      }
    }
  }, [exchangeProductVariant])

  function handleCheckboxChange(e) {
    saveLineItem(e)
    handleChange(e, lineItem?.id)
  }

  const { devices } = useDetectDevices()

  return (
    <ReturnItem>
      <ProductOption>
        <StyledLineItemThumbnail lineItem={lineItem} />
      </ProductOption>
      <DetailsWrapper>
        <DetailValue>
          Return Reason:{' '}
          {`${formatReasonCode(reasonCode || '')}${formatSubReason(
            returnLineItemReview,
          )}`}
        </DetailValue>
        <StyledCheckboxStageTwo
          name='returnType'
          value='REFUND_TO_STORE_CREDIT'
          checked={formData.returnType === 'REFUND_TO_STORE_CREDIT'}
          checkedColor={colors.primary}
          onChange={handleCheckboxChange}
          label={
            <div style={{ display: 'flex' }}>
              <div style={{ marginRight: 5 }}>Refund to store credit </div>
              <div>
                <Bolt />
              </div>
              <p style={{ fontWeight: 500 }}>fastest</p>
              <div style={{ margin: '2px 0 0 5px' }}>
                <Tippy content={storeCreditReturnsMessage}>
                  <span tabIndex='0'>
                    <Info />
                  </span>
                </Tippy>
              </div>
            </div>
          }
        />

        <StyledCheckboxStageTwo
          name='returnType'
          value='REPLACEMENT'
          checked={formData.returnType === 'REPLACEMENT'}
          checkedColor={colors.primary}
          onChange={handleCheckboxChange}
          disabled={outOfStockForReplace}
          label='Exchange with the exact same item'
        />

        <StyledCheckboxStageTwo
          name='returnType'
          value='EXCHANGE'
          checkedColor={colors.primary}
          checked={formData.returnType === 'EXCHANGE'}
          label={
            formData.returnType === 'EXCHANGE' ? (
              <ExchangeWrapper>
                <p style={{ marginRight: 20 }}>
                  Exchange Color:{' '}
                  {devices.mobile && (
                    <p className='exchange'>{exchangeOption?.color}</p>
                  )}
                  {devices.desktop && (
                    <span className='exchange'>{exchangeOption?.color}</span>
                  )}
                </p>
                <p>
                  Size:{' '}
                  {devices.mobile && (
                    <p className='exchange'>{exchangeOption?.size}</p>
                  )}
                  {devices.desktop && (
                    <span className='exchange'>{exchangeOption?.size}</span>
                  )}
                </p>
                <Edit onClick={() => setDrawerOpen(true)}>Edit</Edit>
              </ExchangeWrapper>
            ) : (
              'Exchange with different size or color'
            )
          }
          onChange={() => {
            setDrawerOpen(true)
          }}
          showMarginTop
        />

        <StyledCheckboxStageTwo
          name='returnType'
          value='REFUND'
          checked={formData.returnType === 'REFUND'}
          checkedColor={colors.primary}
          label='Refund to your card'
          onChange={handleCheckboxChange}
          showMarginTop
        />
      </DetailsWrapper>
      {drawerOpen && (
        <Drawer open={drawerOpen} onClose={() => setDrawerOpen(false)}>
          <ExchangeLineItemSelector
            onLineItemSelect={setExchangeVariant}
            lineItem={lineItem}
            startingSize={exchangeOption && exchangeOption.size}
            startingColor={exchangeOption && exchangeOption.color}
            product={contentfulProduct}
            clearValidationError={() => setValidationError('')}
            handleOutOfStock={handleOutOfStock}
          />
          <StyledButton
            contrast
            onClick={handleSubmit}
            style={{ width: '100%' }}
            disabled={outOfStock}
          >
            Submit
          </StyledButton>
        </Drawer>
      )}
    </ReturnItem>
  )
}
