import React, { useEffect } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../../styles/utils'
import useFetch from '../../../hooks/useFetch'

import CloseIcon from '../../../assets/close.svg'
import SpinningLoader from '../../SpinningLoader'

const StyledUploadThumbnail = styled.div`
  position: relative;
`

const DeletePhotoBtn = styled.button`
  z-index: 1;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  padding: 5px;
  background: ${props => props.theme.colors.secondary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(5)};
  }
`

const StyledCloseIcon = styled(CloseIcon)`
  width: 11px;
  path {
    fill: ${props => props.theme.colors.primary};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(11)};
  }
`

const PreviewThumbnailImg = styled.img`
  width: 105px;
  opacity: ${props => (props.opacity ? '0.5' : '1')};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(136)};
  }
`

const UploadThumbnail = ({
  fileName,
  imageApiUrl,
  base64String,
  photos,
  removePhoto,
  setPhotos,
  file,
}) => {
  const { fetchData, response, error, loading } = useFetch()

  useEffect(() => {
    if (!response) return

    const newPhotos = [...photos]
    const objIndex = photos.findIndex(({ name }) => name === fileName)

    newPhotos[objIndex].response = response
    setPhotos(newPhotos)
  }, [response])

  useEffect(() => {
    if (response) return

    const data = new FormData()

    data.append('file', file)

    const options = {
      method: 'post',
      body: data,
    }

    fetchData(imageApiUrl, options)
  }, [file])

  return (
    <StyledUploadThumbnail>
      <DeletePhotoBtn
        type='button'
        aria-label='Remove photo'
        onClick={() => removePhoto(fileName)}
      >
        <StyledCloseIcon />
      </DeletePhotoBtn>
      {loading && <SpinningLoader />}
      <PreviewThumbnailImg src={base64String} opacity={loading ? 1 : 0} />
    </StyledUploadThumbnail>
  )
}

export default UploadThumbnail
