import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import { getCurrentUser } from '../../utils/auth'

import CirceTick from '../../assets/circle_tick.svg'
import { StyledButton } from './commons'

import { getLatestReturnDetailForOrderId } from '../../utils/returns'
import DottedSpinner from '../common/DottedSpinner'
import NewBestSellersSection from '../NewBestSellersSection'
import { StarRating } from '../common/StarRating'
import eventTracker from '../../trackers/EventTracker'
import EventType from '../../trackers/eventType'

const StyledReturnsSummary = styled.div`
  margin: 22px 0 42px;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 25px 11px 42px;
  p {
    font-size: 12px;
    line-height: 20px;
    text-align: center;
  }
  background: #f9f9f9;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(19)} 0 ${desktopVw(43)};
    padding: ${desktopVw(47)} ${desktopVw(241)} ${desktopVw(32)};
    p {
      font-size: ${desktopVw(12)};
      line-height: ${desktopVw(20)};
    }
  }
`

const FeedbackBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  border-top: 0.5px solid ${({ theme }) => theme.colors.silver};
  border-bottom: 0.5px solid ${({ theme }) => theme.colors.silver};
  padding: 70px 0;
  margin: 0 -11px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    text-align: left;
    border: 0.5px solid ${({ theme }) => theme.colors.silver};
    flex-direction: row;
    padding: ${desktopVw(34)} ${desktopVw(43)};
  }
`

const Text = styled.p`
  font-weight: 500;
  font-size: 15px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(22)};
  }
`

const SubText = styled.p`
  font-size: 12px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(17)};
  }
`

const Heading = styled.h1`
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 10px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(36)};
    margin-top: ${desktopVw(17)};
    margin-bottom: ${desktopVw(10)};
  }
`

const AnotherHeading = styled(Heading)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(40)} 0 ${desktopVw(9)};
  }
`

const LabelButton = styled(StyledButton)`
  text-transform: uppercase;
  margin-top: 26px;
  margin-bottom: 26px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(28)};
    margin-bottom: ${desktopVw(28)};
  }
`

const StarContainer = styled.div`
  display: flex;
  margin-top: 28px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
  }
`

const ReturnConfirmation = ({
  orderId,
  recommendedProducts,
  pseudoSuccessMsg,
  returnLabelUrl,
  orders,
  loading,
}) => {
  const user = getCurrentUser()

  if (loading)
    return (
      <div style={{ padding: 25 }}>
        <DottedSpinner />
      </div>
    )

  return (
    <>
      <StyledReturnsSummary>
        <div>
          <CirceTick />
        </div>
        {!pseudoSuccessMsg && (
          <>
            <Heading>Success!</Heading>
            <p>
              Your return{' '}
              <b>
                {getLatestReturnDetailForOrderId(orders, orderId)?.returnId}
              </b>{' '}
              has been started.
            </p>
            <p>We’ve sent a confirmation email to {user?.email}</p>
          </>
        )}
        <AnotherHeading>Next steps</AnotherHeading>
        {pseudoSuccessMsg ? (
          <p>{pseudoSuccessMsg}</p>
        ) : (
          <>
            <p style={{ marginBottom: 9 }}>
              Follow the instruction in your email to complete your return or
              exchange.
            </p>
            <p>
              Print your shipping label below affix it to a package of your
              choice, and drop in the mail. Please note we do not accept items
              that have been worn, washed, damaged or altered.
            </p>
            <>
              {returnLabelUrl && (
                <LabelButton contrast href={returnLabelUrl} target='_blank'>
                  Download Shipping Label
                </LabelButton>
              )}
              <p>
                *Transit time on returns is 5-7 business days. Once received,
                your return will be processed in 72 business hours*
              </p>
            </>
          </>
        )}
      </StyledReturnsSummary>
      <FeedbackBox>
        <div>
          <Text style={{ marginBottom: 6 }}>HOW WAS YOUR EXPERIENCE?</Text>
          <SubText>Help us improve!</SubText>
        </div>
        <StarContainer>
          <StarRating
            onClick={value => {
              eventTracker.track(EventType.ENTER_FEEDBACK, {
                action: 'return_process',
                entity_type: 'rating',
                value,
                context: {
                  return_id: getLatestReturnDetailForOrderId(orders, orderId)
                    ?.returnId,
                },
              })
            }}
          />
        </StarContainer>
      </FeedbackBox>
      {recommendedProducts && (
        <NewBestSellersSection
          json={recommendedProducts.text.json}
          textColor={recommendedProducts.textColor}
          image={recommendedProducts.image}
          products={recommendedProducts.products}
          tracker='returns_process'
          hideImageOnMobile
        />
      )}
    </>
  )
}

export default ReturnConfirmation
