import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { graphql, Link } from 'gatsby'

import { navigate, useLocation } from '@reach/router'
import { desktopVw } from '../../styles/utils'
import StageZeroDrawer from '../../components/Returns/StageZero/StageZeroDrawer'
import { Alert } from '../../components/FormElements'
import useFetch from '../../hooks/useFetch'
import { extractQueryParamFromPageLocation, getCurrentUser } from '../../utils'
import {
  getContentfulProduct,
  getHeading,
  getImageUrls,
  getQuantitiesRemainingToReturn,
  getReturnLineItem,
  isMethodRefund,
  logSentry,
} from '../../utils/returns'
import { StageZero } from '../../components/Returns/StageZero'
import { StyledButton } from '../../components/Returns/commons'
import { StageOne } from '../../components/Returns/StageOne'
import { StageTwo } from '../../components/Returns/StageTwo'
import { MobileLineItemSummaryModal } from '../../components/Returns/MobileLineItemSummaryModal'
import { Timeline } from '../../components/common/Timeline'
import ReturnConfirmation from '../../components/Returns/ReturnConfirmation'
import { zipObject } from '../../utils/build'

const ButtonContainer = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
  }
`

const Container = styled.div`
  padding: 21px 21px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(41)} ${desktopVw(240)};
  }
`

const LinkWrapper = styled.div`
  display: flex;
  text-transform: uppercase;
  font-size: 12px;
  a {
    text-decoration: underline;
    margin: 0 5px;
    &:first-child {
      margin-left: 0;
    }
  }
  p {
    margin-left: 5px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

const Heading = styled.h1`
  font-size: 24px;
  margin: 19px 0 7px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(36)};
    margin: ${desktopVw(24)} 0 ${desktopVw(15)};
  }
`

const Description = styled.p`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    span {
      text-transform: uppercase;
    }
  }
`

const Returns = ({ location, data, orderId }) => {
  const page = data.contentfulAccountPage
  const { nodes } = data.allContentfulProduct
  const { fetchData, response: orders } = useFetch()
  const {
    fetchData: createReturn,
    response: createReturnResponse,
    loading: createReturnLoading,
    error: createReturnError,
  } = useFetch()

  const order =
    (location.state && location.state.order) ||
    (orders ?? []).find(order => order.orderId === Number(orderId))

  const { email } = getCurrentUser()

  useEffect(() => {
    const url = `${process.env.GATSBY_ORDERS_API_ENDPOINT}/service/order?emailId=${email}&limit=50`
    if (!order && email) fetchData(url)

    const showOrderSummary = extractQueryParamFromPageLocation(
      'show_order_summary',
    )
    if (showOrderSummary === 'true') setSummaryModalOpen(true)
  }, [])

  const [lineItems, setLineItems] = useState([])
  const [activeLineItem, setActiveLineItem] = useState(null)
  const [activeLineItemIndex, setActiveLineItemIndex] = useState(null)
  const [activeLineItemsChecked, setActiveLineItemsChecked] = useState([])
  const [contentfulProduct, setContentfulProduct] = useState(null)
  const [summaryModalOpen, setSummaryModalOpen] = useState(false)
  const [validationError, setValidationError] = useState('')

  useEffect(() => {
    if (!order) return
    const { shipments } = order
    let allLineItems = []
    ;(shipments || []).forEach(shipment => {
      if (shipment.lineItems && shipment.lineItems.length > 0) {
        if (
          extractQueryParamFromPageLocation('show_order_summary') === 'true'
        ) {
          allLineItems = allLineItems.concat(shipment.lineItems)
        } else {
          allLineItems = allLineItems.concat(
            shipment.lineItems.filter(
              lineItem => getQuantitiesRemainingToReturn(lineItem) > 0,
            ),
          )
        }
      }
    })
    setLineItems(allLineItems)
  }, [order])

  useEffect(() => {
    if (!activeLineItem) return

    const product = getContentfulProduct(nodes, activeLineItem)

    setContentfulProduct(product)
  }, [activeLineItem])

  const currLocation = useLocation()

  const [stage, setStage] = useState(0)
  const [lineItemPhotos, setLineItemPhotos] = useState({})

  const [errorMsg, setErrorMsg] = useState('')

  const [returnLineItems, setReturnLineItems] = useState([])
  const [formData, setFormData] = useState({})

  const [modalOpen, setModalOpen] = useState(null)

  const [pseudoSuccessMsg, setPseudoSuccessMsg] = useState('')

  const [returnLabelUrl, setReturnLabelUrl] = useState(null)

  const [outOfStockForReplace, setOutOfStockForReplace] = useState(false)

  const user = getCurrentUser()

  useEffect(() => {
    const stageParam = extractQueryParamFromPageLocation('stage')
    const showOrderSummary = extractQueryParamFromPageLocation(
      'show_order_summary',
    )
    if (showOrderSummary === 'true') setSummaryModalOpen(true)
    else if (stageParam) {
      if (returnLineItems.length === 0 && Number(stageParam) !== 0) {
        navigate(`?stage=0`)
      } else {
        setStage(Number(stageParam))
        setSummaryModalOpen(false)
      }
    }
  }, [currLocation])

  const handleCheckboxClick = i => {
    if (activeLineItemsChecked.includes(i)) {
      const lineItemId = lineItems[i]?.id
      const checkedItems = [...activeLineItemsChecked]
      const returnItems = [...returnLineItems]
      const rIdx = returnItems.findIndex(item => item.lineItemId === lineItemId)
      const idx = checkedItems.indexOf(i)
      checkedItems.splice(idx, 1)
      returnItems.splice(rIdx, 1)
      setActiveLineItemsChecked(checkedItems)
      setReturnLineItems(returnItems)
    } else {
      setActiveLineItem(lineItems[i])
      setActiveLineItemIndex(i)
      setModalOpen(true)
      setErrorMsg('')
    }
  }

  const handleReturnCreate = () => {
    const url = `${process.env.GATSBY_ORDERS_API_ENDPOINT}/service/returns/create`

    if ((returnLineItems ?? []).length === 0) return

    const newReturnLineItems = returnLineItems.map(
      ({ lineItem, exchangeOption, ...returnLineItem }) => {
        const isStoreCreditRefund =
          returnLineItem?.returnType === 'REFUND_TO_STORE_CREDIT'
        const isRefundMethod = returnLineItem?.returnType === 'REFUND'

        let refundParameters = {}

        if (isRefundMethod)
          refundParameters = { returnType: 'REFUND', refundMethod: 'CARD' }
        if (isStoreCreditRefund)
          refundParameters = {
            returnType: 'REFUND',
            refundMethod: 'STORE_CREDIT',
          }

        return {
          ...returnLineItem,
          ...refundParameters,
          returnLineItemReview: {
            ...returnLineItem.returnLineItemReview,
            subReason: returnLineItem.returnLineItemReview.subReason,
          },
          restocked: false,
        }
      },
    )

    try {
      const body = {
        orderId,
        returnStatus: 'REQUEST_CREATED',
        customerName: `${user.name} ${user.family_name}`,
        returnLineItems: newReturnLineItems,
      }

      const options = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
        redirect: 'follow',
      }

      createReturn(url, options)
    } catch (error) {
      logSentry(error, user, orderId)
      // handling error gracefully, since this is handled by CS team internally
      setPseudoSuccessMsg(pseudoSuccessMsg)
    }
  }

  useEffect(() => {
    if (!createReturnResponse) return

    if (createReturnError) {
      logSentry(createReturnError, user, orderId)
      // handling error gracefully, since this is handled by CS team internally
      setPseudoSuccessMsg(
        `We've received your return request. Our customer success team will get back to you with the shipping label to send the product(s) back to us within the next 48 hrs.`,
      )

      return
    }

    setReturnLabelUrl(createReturnResponse?.labelUrl)
    if (email)
      fetchData(
        `${process.env.GATSBY_ORDERS_API_ENDPOINT}/service/order?emailId=${email}&limit=50`,
      )
  }, [createReturnResponse])

  const handleChange = (e, lineItemIdParam) => {
    setValidationError('')

    const { target } = e
    const { name } = target
    const value = target.name === 'consent' ? target.checked : target.value

    const lineItemId = lineItemIdParam || activeLineItem?.id

    let newFormData = formData

    if (isMethodRefund(value)) {
      newFormData = zipObject(
        Object.keys(formData),
        Object.values(formData).map(data => {
          if (isMethodRefund(data?.returnType)) {
            return { ...data, returnType: value }
          }
          return data
        }),
      )
    }

    newFormData = {
      ...newFormData,
      [lineItemId]: {
        ...(formData?.[lineItemId] ?? {}),
        [name]: value,
      },
    }

    setFormData(newFormData)
  }

  const saveReturnLineItem = (
    returnLineItem,
    returnContentfulProduct,
    returnQuantity,
  ) => {
    const firstFindReturnLineItemIndex = returnLineItems.findIndex(
      l => l.lineItemId === returnLineItem.lineItemId,
    )

    const existingLineItems = returnLineItems.filter(
      l => l.lineItemId !== returnLineItem.lineItemId,
    )

    const itemsToReturn = [...Array(returnQuantity)].map(_ => ({
      ...returnLineItem,
      imageUrls: getImageUrls([
        ...(lineItemPhotos?.[returnLineItem?.lineItem?.id] ?? []),
      ]),
      lineItem: {
        ...returnLineItem.lineItem,
        returnedQuantity:
          (returnLineItem?.lineItem?.returnedQuantity || 0) + returnQuantity,
      },
    }))

    let updatedLineItems
    if (firstFindReturnLineItemIndex === -1) {
      updatedLineItems = existingLineItems.concat(itemsToReturn)
    } else {
      // to preserve ordering
      updatedLineItems = [
        ...existingLineItems.slice(0, firstFindReturnLineItemIndex),
        ...itemsToReturn,
        ...existingLineItems.slice(firstFindReturnLineItemIndex),
      ]
    }

    if (
      stage === 1 &&
      (updatedLineItems || []).every(rData => !!rData.returnType)
    ) {
      setErrorMsg('')
    }

    if (isMethodRefund(returnLineItem?.returnType)) {
      updatedLineItems = updatedLineItems.map(lineItem => {
        if (isMethodRefund(lineItem?.returnType)) {
          return { ...lineItem, returnType: returnLineItem?.returnType }
        }
        return lineItem
      })
    }

    setReturnLineItems(updatedLineItems)
  }

  const handleContinue = () => {
    if (returnLineItems.length === 0) {
      setErrorMsg('Please select at least one item to continue')

      return
    }

    let newFormData = formData

    const newReturnLineItems = (returnLineItems ?? []).map(lineItem => {
      if (lineItem?.returnType) return lineItem
      newFormData = {
        ...newFormData,
        [lineItem?.lineItemId]: {
          ...newFormData[lineItem?.lineItemId],
          returnType: 'REFUND_TO_STORE_CREDIT',
        },
      }
      return {
        ...lineItem,
        returnType: 'REFUND_TO_STORE_CREDIT',
      }
    })

    setReturnLineItems(newReturnLineItems)
    setFormData(newFormData)

    if (stage === 1) {
      if (returnLineItems.some(rData => !rData.returnType)) {
        if (outOfStockForReplace) {
          setErrorMsg(
            'This item is out of stock, please select one of the available return options',
          )
          return
        }
        setErrorMsg('Please select one of the above options to proceed')
        return
      }
    }

    if (stage === 2) handleReturnCreate()

    setErrorMsg('')
    navigate(`?stage=${stage + 1}`, { replace: stage === 2 })
    setStage(stage + 1)
  }

  const lineItemToShow = location?.state?.lineItem ?? lineItems?.[0]

  const handleSubmitSuccess = () => {
    const checkedItems = [...activeLineItemsChecked]
    checkedItems.push(activeLineItemIndex)
    setActiveLineItemsChecked([...new Set(checkedItems)])
  }

  const handlePhotosChange = photos => {
    const lineItemId = activeLineItem?.id

    setLineItemPhotos({
      ...lineItemPhotos,
      [lineItemId]: photos,
    })
  }

  if (summaryModalOpen) {
    if (!lineItemToShow) return null
    return (
      <MobileLineItemSummaryModal
        onClose={() => {
          window.history.go(-1)
        }}
        order={order}
        lineItem={lineItemToShow}
        page={page}
        lineItems={lineItems}
        summaryModalOpen={summaryModalOpen}
        closeModal={() => setSummaryModalOpen(false)}
        addAddressForm={page.addAddressForm}
      />
    )
  }

  return (
    <Container>
      <LinkWrapper>
        <Link to='/account/my-profile'>Account</Link>/
        <Link to='/account/my-orders-returns'>Orders</Link>/<p>Return</p>
      </LinkWrapper>
      {stage !== 3 && (
        <>
          <Heading>{getHeading(stage)}</Heading>
          <Description>
            Showing product(s) from your{' '}
            <span>order #{order?.orderNumber}</span>
          </Description>
          <Timeline activeStep={stage} />
        </>
      )}
      {stage === 0 && order && (
        <StageZero
          lineItems={lineItems}
          activeLineItemsChecked={activeLineItemsChecked}
          handleCheckboxClick={handleCheckboxClick}
          returnLineItems={returnLineItems}
          setActiveLineItem={setActiveLineItem}
          setActiveLineItemIndex={setActiveLineItemIndex}
          setModalOpen={setModalOpen}
        />
      )}
      {stage === 1 && order && (
        <StageOne
          returnLineItems={returnLineItems}
          saveReturnLineItem={saveReturnLineItem}
          setValidationError={setValidationError}
          handleChange={handleChange}
          formData={formData}
          nodes={nodes}
          setOutOfStockForReplace={setOutOfStockForReplace}
          storeCreditReturnsMessage={
            page?.storeCreditReturnsMessage?.storeCreditReturnsMessage
          }
        />
      )}

      {stage === 2 && order && <StageTwo returnLineItems={returnLineItems} />}
      {stage === 3 && order && (
        <ReturnConfirmation
          returnLineItems={returnLineItems}
          orderId={Number(order?.orderId)}
          recommendedProducts={page.recommendedProducts}
          orders={orders}
          pseudoSuccessMsg={pseudoSuccessMsg}
          returnLabelUrl={returnLabelUrl}
          loading={createReturnLoading}
        />
      )}
      {stage !== 3 && (
        <ButtonContainer>
          <StyledButton contrast onClick={handleContinue}>
            {stage === 2 ? 'Submit' : 'Next'}
          </StyledButton>
          {errorMsg && <Alert message={errorMsg} />}
        </ButtonContainer>
      )}
      {activeLineItem && contentfulProduct && modalOpen && (
        <StageZeroDrawer
          open={modalOpen}
          closeModal={() => setModalOpen(false)}
          lineItem={activeLineItem}
          contentfulProduct={contentfulProduct}
          saveReturnLineItem={saveReturnLineItem}
          onSubmitSuccess={handleSubmitSuccess}
          formData={formData?.[activeLineItem?.id] ?? {}}
          validationError={validationError}
          setValidationError={setValidationError}
          handleChange={handleChange}
          returnLineItem={getReturnLineItem(returnLineItems, activeLineItem)}
          photos={lineItemPhotos?.[activeLineItem?.id] ?? []}
          handlePhotosChange={handlePhotosChange}
        />
      )}
    </Container>
  )
}

export default Returns

export const ReturnsQuery = graphql`
  query Returns($id: String!) {
    contentfulAccountPage(id: { eq: $id }) {
      id
      slug
      title {
        title
      }
      storeCreditReturnsMessage {
        storeCreditReturnsMessage
      }
      pageType
      hero {
        fluid(maxWidth: 3840) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      menuList {
        ...Link
      }
      featuredImage {
        fluid(maxWidth: 640) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      text {
        text
      }
      contact {
        json
      }
      reviewForm {
        title
        submitButtonText
        successMessage {
          json
        }
        contentfulfields {
          ... on ContentfulInput {
            id
            name
            label
            placeholder
            type
            width
          }
          ... on ContentfulRadio {
            id
            type
            name
            label
            range
            radioOptions {
              name
              value
            }
            displayValues
          }
          ... on ContentfulSelect {
            id
            name
            label
            options
            width
          }
        }
      }
      recommendedProducts {
        id
        text {
          json
        }
        products {
          id
          title
          seoTitle
          slug
          subtitle
          metaTags
          shippingInformation {
            shippingInformation
          }
          shippingMetaInfoText {
            shippingMetaInfoText
          }
          productImages {
            ...ProductImages
          }
          colors {
            ...Color
          }
          shopifyProduct {
            ...ShopifyProduct
          }
        }
      }
      addAddressForm {
        title
        submitButtonText
        successMessage {
          json
        }
        contentfulfields {
          ... on ContentfulInput {
            id
            name
            label
            placeholder
            type
            width
            required
          }
          ... on ContentfulSelect {
            id
            name
            label
            options
            width
          }
        }
      }
    }
    allContentfulProduct {
      nodes {
        id
        createdAt
        slug
        title
        subtitle
        bigThumbnailTextColor
        productTypeName
        attributes {
          name
        }
        tags
        gender
        metaTags
        shippingInformation {
          shippingInformation
        }
        shippingMetaInfoText {
          shippingMetaInfoText
        }
        productImages {
          color {
            name
          }
          images {
            id
            description
            fluid(maxWidth: 400) {
              ...GatsbyContentfulFluid_withWebp_noBase64
            }
          }
        }
        colors {
          name
          addBorder
          hexcode
          texture {
            description
            fluid(maxWidth: 50) {
              ...GatsbyContentfulFluid_withWebp_noBase64
            }
          }
        }
        sizeGuide {
          tables {
            id
            title
            tableRows {
              id
              tableData
            }
          }
          title
          information
        }
        shopifyProduct {
          ...ShopifyProduct
        }
      }
    }
  }
`
