import React from 'react'
import { navigate } from '@reach/router'
import styled from 'styled-components'
import Modal from '../Modal'
import Header from '../Header'
import { mobileVw } from '../../styles/utils'
import EditAddress from '../Account/EditShippingAdress'
import { PROVINCE_CODE_MAP } from '../../utils/constants'

const StyledModal = styled(Modal)`
  overflow-y: scroll !important;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px 20px;
  .modal__close_button {
    top: ${mobileVw(85)};
  }
`
const FormContainer = styled.div`
  margin-top: ${mobileVw(48)};
`

const StyledHeader = styled(Header)`
  margin: 0 -20px;
  background: white;
`

const EditAddressModal = ({ openModal, order, closeModal, addAddressForm }) => {
  const showSuccessBanner = addressData => {
    // navigate to my orders and returns and pass msg for banner using state
    const successBannerHtml = {
      successHTML: `<div style="display: flex;justify-content: space-between;flex-direction: column">
      <div style="font-weight: 500; margin-bottom: 5px; font-size: ${mobileVw(
        12,
      )}; line-height: ${mobileVw(
        16,
      )}"><b>Shipping address has been updated</b></div>
      <div style="font-weight: 400;font-size: ${mobileVw(
        10,
      )}; line-height: ${mobileVw(13)}">New Shipping information is ${
        addressData.address1
      }, ${addressData.city}, ${PROVINCE_CODE_MAP[addressData.province]}, ${
        addressData.zip
      }</div>
      </div>`,
    }
    navigate(`/account/my-orders-returns/`, {
      state: { successBannerHtml, updatedOrder: order?.orderNumber },
    })
    closeModal()
  }
  return (
    <StyledModal open={openModal} onClose={closeModal}>
      <StyledHeader />
      <FormContainer>
        <EditAddress
          open={openModal}
          closeModal={() => {
            closeModal()
          }}
          addAddressForm={addAddressForm}
          activeOrder={order}
          reloadPageData={showSuccessBanner}
        />
      </FormContainer>
    </StyledModal>
  )
}

export default EditAddressModal
