import React, { useEffect, useState } from 'react'
import { navigate } from '@reach/router'
import styled from 'styled-components'
import LineItemThumbnail from '../Account/LineItemThumbnail'
import { StyledButton } from './commons'
import ReviewFormModal from '../ReviewFormModal'
import {
  getLatestReturnDetailForLineItem,
  getOrderReturnLineItem,
  getOrderTrackingLineItemsByShipment,
  getReturnLineItems,
  getReturnsRoute,
  getCancellationRoute,
} from '../../utils/returns'
import Modal from '../Modal'
import useShopifyProduct from '../../hooks/useShopifyProduct'
import useContentfulProduct from '../../hooks/useContentfulProduct'
import QuickShopModal from '../QuickShopModal'
import { groupBy } from '../../utils'
import Header from '../Header'
import { mobileVw } from '../../styles/utils'
import { getTrackingRoute } from '../../utils/tracking'
import EditAddressModal from './EditAddressModal'

const StyledModal = styled(Modal)`
  overflow-y: scroll !important;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px 20px;
  .modal__close_button {
    top: ${mobileVw(85)};
  }
`

const StyledBuyAgain = styled(QuickShopModal)`
  .modal__close_button {
    top: 20px;
  }
`

const Wrapper = styled.div`
  padding: 25px 25px;
`

const Img = styled.img`
  width: 49px;
  height: 64px;
  background-color: grey;
  margin-right: 13px;
  object-fit: cover;
`

const SubHeading = styled.p`
  font-size: 12px;
  font-weight: 500;
  border-top: 1px solid #e0e0e0;
  padding-top: 30px;
  margin-top: 30px;
  margin-bottom: 15px;
`

const StyledHeader = styled(Header)`
  margin: 0 -20px;
`

export function MobileLineItemSummaryModal({
  lineItem,
  onClose,
  order,
  page,
  lineItems,
  summaryModalOpen,
  closeModal,
  addAddressForm,
}) {
  const [modalOpen, setModalOpen] = useState(null)
  const [sizes, setSizes] = useState([])
  const [shopifyColors, setShopifyColors] = useState([])
  const [openModal, setOpenModal] = useState(false)

  const { product } = useShopifyProduct(lineItem.productHandle)
  const { product: contentfulProduct } = useContentfulProduct(
    lineItem.productId,
  )

  const [buyAgainModalOpen, setBuyAgainModalOpen] = useState(false)

  useEffect(() => {
    if (!product) {
      return
    }

    const sizeOption = product.options.find(({ name }) => name === 'Size')
    if (sizeOption) {
      setSizes(sizeOption.values)
    }

    const colorOption = product.options.find(({ name }) => name === 'Color')
    if (colorOption) {
      setShopifyColors(colorOption.values)
    }
  }, [product])

  if (!lineItem) return null

  const returnLineItems = getReturnLineItems(order)

  const returnCompletedLineItems = returnLineItems.filter(
    lineItem => lineItem.status === 'PROCESSED',
  )
  const returnStartedLineItems = returnLineItems.filter(
    lineItem => lineItem.status !== 'PROCESSED',
  )
  const returnCompletedLineItemsByReturnId = groupBy(
    returnCompletedLineItems,
    'returnId',
  )
  const returnStartedLineItemsByReturnId = groupBy(
    returnStartedLineItems,
    'returnId',
  )

  const returnLineItem = getOrderReturnLineItem(order, lineItem.id)

  const orderTrackingLineItemsByShipment = getOrderTrackingLineItemsByShipment(
    order,
    returnLineItems,
  )

  const latestReturnDetailForLineItem = getLatestReturnDetailForLineItem(
    order,
    lineItem.id,
  )

  let updatedLineItems = orderTrackingLineItemsByShipment.reduce(
    (lineItems, shipment) => [...lineItems, ...(shipment?.lineItems ?? [])],
    [],
  )

  if (
    returnLineItem?.status === 'PROCESSED' ||
    (latestReturnDetailForLineItem?.labelUrl &&
      latestReturnDetailForLineItem?.returnStatus === 'REQUEST_CREATED')
  ) {
    if (returnLineItem?.status === 'PROCESSED')
      updatedLineItems =
        returnCompletedLineItemsByReturnId?.[returnLineItem?.returnId] ?? []
    else
      updatedLineItems =
        returnStartedLineItemsByReturnId?.[returnLineItem?.returnId] ?? []
  }

  const otherLineItems =
    updatedLineItems.filter(lItem => lItem?.id !== lineItem?.id) ?? []

  const isOrderCancellable = lineItem?.fulfillableQuantity > 0

  const isShipmentAddressEditable =
    (!order.shipments ||
      (Array.isArray(order.shipments) && order.shipments.length === 0)) &&
    (order?.unallocatedLineItems ?? []).length > 0

  return (
    <StyledModal open={summaryModalOpen} onClose={onClose}>
      <StyledHeader />
      {contentfulProduct && product && (
        <StyledBuyAgain
          product={product}
          contentfulProduct={contentfulProduct}
          shopifyProduct={product}
          title={lineItem.title}
          variants={product && product.variants.edges}
          sizes={sizes}
          open={buyAgainModalOpen}
          onClose={() => setBuyAgainModalOpen(false)}
          tracker='returns_process'
          startingColor={lineItem?.color}
          shopifyColors={shopifyColors}
          showMenuBar={false}
          showCloseBtn
        />
      )}
      {openModal && (
        <EditAddressModal
          openModal={openModal}
          order={order}
          closeModal={() => {
            setOpenModal(false)
          }}
          addAddressForm={addAddressForm}
        />
      )}
      <Wrapper>
        {lineItem && <LineItemThumbnail lineItem={lineItem} />}
        <div style={{ display: 'flex', marginTop: 20 }}>
          <StyledButton
            contrast
            style={{ marginRight: 9 }}
            onClick={() => setBuyAgainModalOpen(true)}
          >
            Buy it again
          </StyledButton>
          <StyledButton onClick={() => setModalOpen(true)}>
            Write a review
          </StyledButton>
          <ReviewFormModal
            open={modalOpen}
            reviewForm={page.reviewForm}
            closeModal={() => setModalOpen(false)}
            productId={lineItem?.productId}
            productName={lineItem?.title}
          />
        </div>
        {latestReturnDetailForLineItem?.labelUrl ? (
          <div>
            <StyledButton
              href={latestReturnDetailForLineItem?.labelUrl}
              target='_blank'
            >
              Print Label
            </StyledButton>
            {latestReturnDetailForLineItem?.trackingUrl &&
              returnLineItem?.status !== 'PROCESSED' && (
                <StyledButton
                  href={latestReturnDetailForLineItem?.trackingUrl}
                  target='_blank'
                >
                  Track Return
                </StyledButton>
              )}
          </div>
        ) : !isOrderCancellable ? (
          <>
            <StyledButton
              to={getTrackingRoute(
                order,
                (order?.shipments ?? []).find(
                  shipment => shipment.fulfillmentId === lineItem.fulfillmentId,
                ),
              )}
            >
              Track Package
            </StyledButton>
            <StyledButton
              onClick={() => {
                navigate(`${getReturnsRoute(order)}?stage=0`)
                closeModal()
              }}
            >
              Return or Replace Items
            </StyledButton>
          </>
        ) : (
          <div>
            <StyledButton
              onClick={() => {
                navigate(`${getCancellationRoute(order)}`)
                closeModal()
              }}
            >
              Cancel Order
            </StyledButton>
            {isShipmentAddressEditable && (
              <StyledButton
                onClick={() => {
                  setOpenModal(true)
                }}
              >
                Edit Address
              </StyledButton>
            )}
          </div>
        )}
        {(otherLineItems ?? []).length > 0 && (
          <>
            <SubHeading>{`Other items in this ${
              latestReturnDetailForLineItem?.labelUrl ? 'return' : 'shipment'
            }`}</SubHeading>
            <div>
              {(otherLineItems ?? []).map(lineItem => (
                <Img key={lineItem.id} loading='lazy' src={lineItem.image} />
              ))}
            </div>
          </>
        )}
      </Wrapper>
    </StyledModal>
  )
}
