import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'

import { desktopVw } from '../styles/utils'

import BackArrowIcon from '../assets/back-arrow.svg'

const StyledBackLink = styled.div`
  display: block;
  font-size: 10px;
  text-transform: uppercase;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const BackArrow = styled(BackArrowIcon)`
  margin-right: 6px;
  width: 13px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(6)};
    width: ${desktopVw(20)};
  }
`

const BackLink = ({ className, text }) => {
  return (
    <StyledBackLink className={className} onClick={() => window.history.go(-1)}>
      <BackArrow />
      <span>{text}</span>
    </StyledBackLink>
  )
}

export default BackLink
