import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../../styles/utils'

import useFetch from '../../../hooks/useFetch'

import { captureWarning } from '../../Sentry/utils'
import { colors } from '../../../styles/theme'

const StyledReturnsSummaryCard = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(59)};
    flex-direction: row;
  }
`

const RefundDetails = styled.ul``

const RefundDetailsItem = styled.li`
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 24px;

  &:last-child {
    border-bottom: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(18)};
    justify-content: end;
  }
`

const RefundDetailTitle = styled.span`
  font-size: 12px;
  line-height: 1;
  font-weight: 500;
  margin-right: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

const RefundDetailTitleSummary = styled(RefundDetailTitle)`
  font-weight: 500 !important;
  font-size: 15px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(15)};
  }
`

const Body = styled.div`
  ${RefundDetailTitle} {
    font-weight: normal;
  }
  ${RefundDetailsItem} {
    margin-bottom: 20px;
  }
  width: 100%;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: unset;
    ${RefundDetailsItem} {
      width: ${desktopVw(290)};
      display: flex;
      justify-content: space-between;
    }
  }
`

const RefundShortDesc = styled.p`
  margin-bottom: 5px;
`

const RefundDetail = styled.div`
  font-size: 12px;
  text-align: left;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    max-width: ${desktopVw(280)};
  }
`

const RefundTerms = styled.p`
  font-size: 10px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const RefundDetailSummary = styled(RefundDetail)`
  font-weight: 500 !important;
  font-size: 15px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(15)};
  }
`

const initialSummaryState = {
  subtotal: 0,
  taxRefund: 0,
  refundEstimate: 0,
  discount: 0,
}

const ReturnsSummaryCard = ({ returnLineItems }) => {
  const { fetchData, response, error, loading } = useFetch()

  useEffect(() => {
    const listReturnLineItemRequests = []
    let orderId

    returnLineItems.forEach(rLineItem => {
      if (
        rLineItem.returnType === 'REFUND' ||
        rLineItem.returnType === 'REFUND_TO_STORE_CREDIT'
      ) {
        ;({ orderId } = rLineItem.lineItem)
        listReturnLineItemRequests.push({
          lineItemId: rLineItem.lineItem.id,
          returnType: 'REFUND',
        })
      }
    })

    if (!orderId) return

    const url = `${process.env.GATSBY_ORDERS_API_ENDPOINT}/service/returns/refund-amount`

    const body = { orderId, listReturnLineItemRequests }

    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body),
    }

    fetchData(url, options)
  }, [])

  const [refundSummary, setRefundSummary] = useState({ ...initialSummaryState })

  useEffect(() => {
    if (!response) return
    if (!response.error) {
      const summary = { ...initialSummaryState }
      ;(response?.lineItemRefunds ?? []).forEach(refundItem => {
        summary.subtotal +=
          refundItem?.refundDistribution?.ITEM_PRICE_REFUND?.refundAmount ?? 0
        summary.taxRefund +=
          refundItem?.refundDistribution?.TAX_REFUND?.refundAmount ?? 0
        summary.refundEstimate += refundItem?.totalRefund?.refundAmount ?? 0
        summary.discount +=
          refundItem?.refundDistribution?.DISCOUNT_REFUND?.refundAmount ?? 0
      })
      setRefundSummary(summary)
    } else if (response.error) {
      captureWarning(
        `Issue with fetching data from refund calculator - ${response.error}`,
      )
    }
  }, [response])

  return (
    <StyledReturnsSummaryCard>
      <RefundDetailsItem>
        <RefundDetailTitle>Return Method:</RefundDetailTitle>
        <RefundDetail>
          <RefundShortDesc>Mail in Items*</RefundShortDesc>
          <RefundTerms>
            We guarantee free returns of most items while keeping our prices low
            by consolidating parcels across the country. Please expect transit
            times of 5-7 business days. Once received, your return will be
            processed within 72 business hours.
          </RefundTerms>
        </RefundDetail>
      </RefundDetailsItem>
      <Body>
        <RefundDetails>
          <RefundDetailsItem>
            <RefundDetailTitle>Subtotal:</RefundDetailTitle>
            <RefundDetail>{`$${refundSummary.subtotal.toFixed(
              2,
            )}`}</RefundDetail>
          </RefundDetailsItem>

          <RefundDetailsItem>
            <RefundDetailTitle>Tax Refund:</RefundDetailTitle>
            <RefundDetail>{`$${refundSummary.taxRefund.toFixed(
              2,
            )}`}</RefundDetail>
          </RefundDetailsItem>

          <RefundDetailsItem>
            <RefundDetailTitle>Discount Refund:</RefundDetailTitle>
            <RefundDetail>{`-$${(-1 * refundSummary.discount).toFixed(
              2,
            )}`}</RefundDetail>
          </RefundDetailsItem>

          <RefundDetailsItem>
            <RefundDetailTitle>Return Label Fee:</RefundDetailTitle>
            <RefundDetail style={{ color: colors.shamrockGreen }}>
              FREE
            </RefundDetail>
          </RefundDetailsItem>

          <RefundDetailsItem>
            <RefundDetailTitleSummary>
              Refund Estimate:
            </RefundDetailTitleSummary>
            <RefundDetailSummary>{`$${refundSummary.refundEstimate.toFixed(
              2,
            )}`}</RefundDetailSummary>
          </RefundDetailsItem>
        </RefundDetails>
      </Body>
    </StyledReturnsSummaryCard>
  )
}

export default ReturnsSummaryCard
