import useDetectDevices from '../../../hooks/useDetectDevices'
import { ReturnItem, StyledLineItemThumbnail } from '../commons'
import ReturnsSummaryCard from './ReturnsSummaryCard'
import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../../styles/utils'
import { mapReturnType } from '../../../utils/returns'

const StyledLineItemThumbnailFinalStage = styled(StyledLineItemThumbnail)`
  .line_item_thumbnail__wrapper {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    margin-top: 0;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    .line_item_thumbnail__wrapper {
    }
  }
`

const ReturnType = styled.p`
  font-size: 10px;
  margin-top: 5px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    font-size: ${desktopVw(12)};
  }
  span {
    font-weight: 500;
  }
`
const Price = styled.p`
  font-weight: 500;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

export function StageTwo({ returnLineItems }) {
  const { devices } = useDetectDevices()

  return (
    <>
      {returnLineItems.map(({ lineItem, exchangeOption, returnType }, i) => (
        <ReturnItem key={lineItem.id}>
          <StyledLineItemThumbnailFinalStage
            lineItem={lineItem}
            showPrice={false}
            showHighlightedPrice={!devices.desktop}
          >
            <ReturnType>
              {returnType === 'EXCHANGE' ? (
                <div
                  style={{
                    display: 'flex',
                    flexDirection: devices.desktop ? 'row' : 'column',
                  }}
                >
                  <p style={{ marginRight: 10 }}>
                    Exchange Color: <span>{exchangeOption?.color}</span>
                  </p>
                  <p>
                    Size: <span>{exchangeOption?.size}</span>
                  </p>
                </div>
              ) : (
                mapReturnType(returnType)
              )}
            </ReturnType>
          </StyledLineItemThumbnailFinalStage>

          {devices.desktop && (
            <Price>
              ${parseFloat(exchangeOption?.price || lineItem?.price).toFixed(2)}
            </Price>
          )}
        </ReturnItem>
      ))}
      <ReturnsSummaryCard returnLineItems={returnLineItems} />
    </>
  )
}
